import React, { Component } from 'react';

class RadioConvertUnits extends Component {
	
	render(){

		
	return (
		<form className="RadioBox"><table><tbody>
		
		<tr>
			<td>
				<input type="radio" name="units1" value="ft" onChange={this.props.onChange} checked={this.props.units === "ft"} /></td>
				<td>Imperial (feet)</td><td>&#160;&#160;</td>
		
			<td>
			<input type="radio" name="units2" value="m"  onChange={this.props.onChange} checked={this.props.units === "m"} /></td>
				<td>Metric (meters)</td>
		</tr>
				
		</tbody></table></form>
	)}
}

export default RadioConvertUnits;